<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process284"></a>

<p><br></p>
<p><span class="rvts17">结局4. 众生平等</span></p>
<p><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1538.jpg" display="block" background-size="contain|cover"><span class="rvts39"></span>
<br><span class="rvts21">该结局为选择了把神性分享给所有的天下人。</span>
<br><span class="rvts21">这样以来，所有人都成文了源力者。在起初阶段，所有的源力者和平相处，并且共同对抗神王。但是越到后来，源力者之间产生了各种纷争，天下再次不太平起来。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return {};
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



